import isEqual from "lodash/isEqual";
import { PlayBackType } from "@/components/player/types/playerType";

/**
 * 
 * @param arr 
 * @returns
 */
export const shuffle = function (arr: []) {
  for (let i = arr.length - 1; i >= 0; i--) {
    const randomIndex = Math.floor(Math.random() * (i + 1));
    const itemAtIndex = arr[randomIndex];

    arr[randomIndex] = arr[i];
    arr[i] = itemAtIndex;
  }
  return arr;
};

/**
 *
 * @param arr 
 * @param current 
 * @returns
 */
export function playerShuffle<T>(arr: T[], current: T) {
  /**  */
  const rangeArray = arr.filter((item) => !isEqual(item, current));
  for (let i = rangeArray.length - 1; i >= 0; i--) {
    const randomIndex = Math.floor(Math.random() * (i + 1));
    const itemAtIndex = rangeArray[randomIndex];

    rangeArray[randomIndex] = rangeArray[i];
    rangeArray[i] = itemAtIndex;
  }
  return rangeArray[0];
}

/**
 * 
 * @param arr 
 * @param current 
 * @param playBackType 
 */
export function playerNextReOrder<T>(
  arr: T[],
  current: T,
  playBackType: PlayBackType
): T | undefined {
  const findIndex = arr.findIndex((item) => isEqual(item, current));
  if (findIndex === -1) {
    return arr[0];
  }
  const isLastOne = findIndex === arr.length - 1;

  /**  */
  if (playBackType === "icon-24gf-playlist3") {
    if (isLastOne) {
      return undefined;
    }
    return arr[findIndex + 1];
  } else if (
    playBackType === "icon-xunhuanbofang" ||
    playBackType === "icon-danquxunhuan"
  ) {
    /**  */
    if (isLastOne) {
      return arr[0];
    }
    return arr[findIndex + 1];
  } else if (playBackType === "icon-random") {
    return playerShuffle(arr, current);
  }
}
